<template>
  <div class="sr-dialog-select">
    <el-input
      v-model="project.projectName"
      readonly
      placeholder="请选择项目"
      suffix-icon="el-icon-caret-bottom"
      @focus="handleFocus"
    ></el-input>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      append-to-body
      :width="dialogWidth"
      center
      custom-class="dialog_select"
      v-if="dialogVisible"
      :before-close="handleClose"
    >
      <div class="dialog_select_header">
        <div class="search">
          <slot name="search">
            <el-input
              v-model="keyword"
              placeholder="请输入项目名称/编号"
            ></el-input>
          </slot>
          <span class="buttonBox" v-if="buttonFlag">
            <sr-popover content="搜索">
              <el-button
                size="small"
                type="primary"
                icon="el-icon-search"
                circle
                @click="search"
              ></el-button>
            </sr-popover>
            <sr-popover content="重置">
              <el-button
                size="small"
                icon="el-icon-refresh"
                circle
                @click="refresh"
              ></el-button>
            </sr-popover>
          </span>
        </div>
      </div>
      <SrTable
        :data="table.data"
        :config="table.tableConfig"
        :page="table.page"
        @pageChanged="pageChanged"
        @clickRow="clickRow"
        @rowDblclick="rowDblclick"
        @selected="selected"
        @selectAll="selected"
      />
      <div slot="footer" class="dialog-footer" v-if="footerFlag">
        <el-button size="small" round type="primary" @click="confirm"
          >确定</el-button
        >
        <el-button size="small" round @click="close">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "SrDialogSelect",
  props: {
    buttonFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    footerFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    dialogTitle: {
      type: String,
      default: "选择项目"
    },
    dialogWidth: {
      type: String,
      default: () => {
        return "800px";
      }
    },
    getList: {
      type: Function,
      default: () => {
        return function() {};
      }
    },
    // 不固定的请求接口的参数
    params: Object,
    hasChildrenFlag: {
      type: Boolean,
      default() {
        return true;
      }
    },
    tableConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    // 多选时再次打开弹框已经选中的数据
    selectData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 多选最多可选几个
    max: {
      type: Number,
      default: () => {
        return 20;
      }
    },
    header: {
      type: Array,
      default: () => {
        return [
          {
            label: "项目名称",
            prop: "projectName"
          },
          {
            label: "项目编号",
            prop: "projectCode"
          }
        ];
      }
    },
    isExpert: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    selectionConfig: {
      type: Object,
      default: () => {
        return {
          display: false
        };
      }
    }
  },
  data() {
    return {
      dialogVisible: false,
      project: {
        projectName: null,
        projectCode: null,
        projectId: null
      },
      keyword: null,
      table: {
        data: [],
        tableConfig: {
          load: this.load,
          highlightCurrentRow: true,
          selectionConfig: this.selectionConfig,
          header: this.header,
          indexConfig: {
            display: true,
            label: "序列",
            width: "52"
          }
        },
        page: {
          // 分页配置
          total: 0, //总条数
          pages: 0, //总页数
          pageSize: 10, //每页展示多少个
          pageNum: 1, //当前页
          layout: "prev, pager, next, jumper"
        }
      },
      selectObj: null
    };
  },
  mounted() {
    Object.keys(this.tableConfig).forEach(item => {
      this.$set(this.table.tableConfig, item, this.tableConfig[item]);
    });
  },
  methods: {
    close() {
      this.$emit("close", "");
      this.dialogVisible = false;
    },
    handleClose(done) {
      this.$emit("close", "");
      done();
    },
    load(tree, treeNode, resolve) {
      let params = {
        pageNum: 1,
        pageSize: 999,
        isSubproject: 1,
        mainProjectId: tree.id
      };
      this.getList(params).then(res => {
        if (res.msg.code === "0000") {
          resolve(res.data.records);
        }
      });
    },
    search() {
      this.table.page.pageNum = 1;
      this.queryList();
    },
    // 点击按钮重置
    refresh() {
      this.keyword = null;
      this.table.page.pageNum = 1;
      this.queryList();
    },
    // 输入框获得焦点之后打开dialog弹框
    handleFocus() {
      this.dialogVisible = true;
      this.queryList();
    },
    // 点击单行事件
    clickRow(val) {
      if (this.table.tableConfig.highlightCurrentRow) {
        this.selectObj = val;
      }
    },
    // 单行双击事件
    rowDblclick(val) {
      if (this.table.tableConfig.highlightCurrentRow) {
        this.selectObj = val;
        this.confirm();
      }
    },
    //页数发生改变
    pageChanged(val) {
      this.table.page.pageNum = val;
      this.queryList();
    },
    // 搜索条件查询事件
    queryList() {
      this.$emit("queryList");
      let params = {
        pageNum: this.table.page.pageNum,
        pageSize: this.isExpert ? 999 : 10,
        keyword: this.keyword,
        ...this.params
      };
      this.getList(params).then(res => {
        if (res.msg.code === "0000") {
          this.table.data = res.data.records.map(item => {
            if (!this.hasChildrenFlag) {
              item.hasChildren = false;
            }
            return item;
          });
          this.table.page.total = res.data.total;
        }
      });
    },
    // 确认选中
    confirm() {
      if (this.table.tableConfig.selectionConfig.display) {
        // 多选
        if (this.selectObj) {
          if (this.selectObj.length + this.selectData.length > this.max) {
            this.$message({
              message: "超出设定人数，请核对专家人数！",
              type: "warning"
            });
          } else {
            this.project = this.selectObj;
            this.$emit("select", this.project);
            this.dialogVisible = false;
          }
        } else {
          this.dialogVisible = false;
        }
      } else {
        // 单选
        this.project = this.selectObj;
        this.$emit("select", this.project);
        this.dialogVisible = false;
      }
    },
    // 开启多选框之后的选择事件
    selected(val) {
      this.selectObj = val;
    }
  }
};
</script>



